import $ from '../core';

$.prototype.modal = function (created) {
  for(let i = 0; i < this.length; i++){
      const target = this[i].getAttribute('data-target'),
            scroll1 = scroll();
    $(this[i]).click((e) =>{
      e.preventDefault();
      $(target).fadeIn(500);
      document.body.style.overflow = 'hidden';
      document.body.style.marginRight = `${scroll1}px`;

    });
   function scroll() {
    const div = document.createElement('div');
    div.style.cssText = `
    width: 50px;
    height: 50px;
    visibility: hidden;
    overflow-Y: scroll;`;
    document.body.append(div);
    const res = div.offsetWidth - div.clientWidth;
    div.remove();
    return res;
   }
    const closeElements = document.querySelectorAll(`${target} [data-close]`);
    closeElements.forEach(elem =>{
      $(elem).click(() =>{
        $(target).fadeOut(500);
        document.body.style.overflow = '';
        document.body.style.marginRight = `${0}px`;
        if (created) {
          document.querySelector(target).remove();
        }
      });
    });
  
    $(target).click(e => {
      if (e.target.classList.contains('modal')) {
        $(target).fadeOut(500);
        document.body.style.overflow = '';
        if (created) {
          document.querySelector(target).remove();
        }
      }
    });
  }

};

$('[data-toggle="modal"]').modal();

$.prototype.createModal = function({text, btns} = {}) {
 const {settings} = btns;
  for (let i = 0; i < this.length; i++) {
    let modal = document.createElement('div');
    modal.classList.add('modal');
    modal.setAttribute('id', this[i].getAttribute('data-target').slice(1));
    
    const buttons = [];
    for(let j = 0; j < btns.count; j++){
      let btn = document.createElement('button');
      btn.classList.add('btn', ...settings[j][1]);
      btn.textContent = settings[j][0];

      if(settings[j][2]){
        btn.setAttribute('data-close', 'true');
      }
       
      if(settings[j][3] && typeof(settings[j][3]) === 'function'){
        btn.addEventListener('click', settings[j][3]);
      }
     buttons.push(btn);
    }

    modal.innerHTML = `
    <div class="modal-dialog">
    <div class="modal-content">
  
      <button class="close" data-close>
        <span>&times;</span>
      </button>
  
      <div class="modal-header">
        <div class="modal-title">
          ${text.title}
        </div>
  
        <div class="modal-body">
          ${text.body} 
        </div>
  
        <div class="modal-footer">
          
        </div>
  
      </div>
    </div>
  </div>`;
    modal.querySelector(".modal-footer").append(...buttons);
    document.body.appendChild(modal);
    $(this[i]).modal(true);
    $(this[i].getAttribute('data-target')).fadeIn(500);
  }
}